body {
  color: #fff;
  background-color: #000;
  margin: 0;
  padding: 0;
  font-family: avenir next;
}

.startpage-container {
  height: 100vh;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.startpage-container--moveUp {
  height: min-content;
}

.header {
  color: #8fffd2;
  margin-top: -30px;
  font-family: Fredoka One;
  font-size: 50px;
}

.header--moveUp {
  margin-top: 25px;
}

.header:hover {
  cursor: pointer;
}

.input {
  grid-template-rows: auto auto;
  grid-template-columns: auto;
  gap: 10px;
  margin-bottom: 20px;
  display: grid;
}

.input__text {
  width: 50vw;
  text-align: center;
  border-style: none;
  border-radius: 5px;
  padding: 5px;
}

@media screen and (min-width: 768px) {
  .input__text {
    width: 25vw;
  }
}

.input__text::placeholder {
  color: #494949;
  padding: 10px;
}

.input__text:focus {
  outline: none;
  box-shadow: 0 0 20px 5px #efedf052;
}

.input__btn {
  cursor: pointer;
  background-color: #49cc9a;
  border-style: none;
  border-radius: 5px;
  grid-column-start: col-start-0;
}

.input__btn:hover {
  background-color: #58ffbf;
}

.input__error {
  color: #d7d7e4;
  grid-row-start: 2;
  grid-column-start: 1;
}

.search-result {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.search-result__container {
  width: 100%;
  background-color: #151515;
  flex-wrap: wrap;
  justify-content: space-around;
  gap: 0;
  display: flex;
}

.movie {
  width: 40%;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  margin: 5px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .movie {
    width: 17%;
  }
}

.movie__title {
  text-align: center;
  width: 80%;
  color: #fff;
  text-shadow: 1px 1px #000, 1px 1px #000, 1px 1px #000, 1px 1px #000;
  font-family: gill sans;
  font-size: 10px;
  display: none;
}

@media screen and (min-width: 768px) {
  .movie__title {
    width: 80%;
    font-size: 12px;
  }
}

.movie__poster {
  width: auto;
  height: 100%;
  width: 100%;
  border: 1px solid #ffffff4d;
  margin-bottom: 5px;
}

@media screen and (min-width: 768px) {
  .movie__poster {
    width: 150px;
    height: 225px;
    width: 100%;
    height: 100%;
    margin-bottom: 20px;
  }
}

.movie__poster:hover {
  cursor: pointer;
  box-shadow: 0 20px 54px 20px #5353531f, 0 2px 30px 5px #efedf052;
}

.nextpageBtn {
  background-color: #49cc9a;
  border-style: none;
  border-radius: 5px;
  margin-top: 2rem;
  margin-bottom: 2rem;
  padding: .3rem 1rem;
  display: block;
}

.nextpageBtn:hover {
  background-color: #58ffbf;
}

.modal {
  z-index: 1;
  width: 100vw;
  min-height: 100vh;
  background-color: #1a1919f2;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.modal__container {
  min-height: 100vh;
  width: 100%;
  background-color: #000;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
}

.modal__content {
  text-align: center;
  width: 80vw;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.modal__img {
  margin-top: 10px;
}

.modal__title, .modal__year, .modal__director, .modal__actors, .modal__genre {
  margin: 0;
}

@media screen and (min-width: 768px) {
  .modal__container {
    z-index: 1;
    width: 50%;
    min-height: 400px;
    border: 1px solid #42a087;
    border-radius: 10px;
    flex-direction: row;
    padding: 10px;
    display: flex;
  }

  .modal__img {
    width: 37%;
    height: min-content;
    align-self: center;
    padding: 5px;
  }

  .modal__content {
    flex-direction: column;
    margin-top: 10px;
    padding: 10px;
    display: flex;
  }

  .modal__title, .modal__year, .modal__director, .modal__actors, .modal__genre {
    margin: 0;
  }
}

html {
  scroll-behavior: smooth;
}

.hide {
  display: none;
}

